import {GetDataList} from '@/api/class-center'
export default {
	props:{
		workclassid:Number
	},
	data(){
		return{
			baseUrl:this.$store.state.SRC+"/config/app",
			photoList:[],//作品列表
			getParameters:{
				a:'getplaytime',
				playtime:'works',//小组‘group’，问答‘ask’，作品‘works’，资讯‘information’
				pageindex:1,
				pagesize:10,
				key:'',
				workclassid:0,//作品的分类id
			},
			pageCount:0,
			refreshStatus:false,
			height:0
		}
	},
	watch:{
		'workclassid':{
			handler(workclassid){
				this.getParameters.workclassid = this.workclassid
				this.loadPage()
			},
			deep:true
		}
	},
	mounted(){
		uni.showLoading({
		    title: '加载中'
		});
		uni.getSystemInfo({
			success:res=>{
				this.height = res.windowHeight-88
			}
		})
		this.loadPage()
	},
	methods: {
		onRefresh(){
			this.loadPage()
		},
		openUrl(url){
			uni.navigateTo({
			    url:url
			});
		},
		scrolltolower(){
			this.getParameters.pageindex++
			if(this.getParameters.pageindex <= this.pageCount){
				this.loadPage('more')
			}
		},
		//初始数据的请求
		loadPage(status){
			if(this.loading){
				return
			}
			this.loading = true
			if(status=='more'){
				GetDataList(this.getParameters).then(res=>{
					if(res.result){
						this.photoList = this.askList.concat(res.worksmodel.data)
					}
					this.loading = false
				})
			}else{
				this.getParameters.pageindex=1
				GetDataList(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.worksmodel.pageCount
						this.photoList = res.worksmodel.data
					}else{
						this.newsList = []
						this.pageCount = 0
					}
					uni.hideLoading();
					this.loading = false
					setTimeout(()=>{
						this.refreshStatus = false
					},1000)
				})
			}
		}
		
	}
}
